<template>
  <div class="j-module-content j-maillist mllist-list height100 ">

    <section class="content content-list height100" style="background-color: #fff;background:rgba(255,255,255,0.8)" v-loading="listLoading">
      <el-form :inline="true" :model="filters" style="padding:4px 0 4px 4px;">
        <el-row>
          <el-col style="text-align:left">
            <el-form-item style="margin-bottom: 0px!important;">
              <el-button plain size="small" type="primary" icon="el-icon-download" :disabled="this.sels.length===0"  @click="zipDownload">{{plang.FILE_P_DOWNLOAD}}</el-button>
              <el-button plain size="small" type="primary" icon="el-icon-message" :disabled="this.sels.length===0" @click="$parent.sendMail_net('more',sels)">{{plang.FILE_P_SEND}}</el-button>
              <el-button plain size="small" type="primary" :disabled="this.sels.length===0" @click="moveFormShow">{{plang.FILE_A_TOP}}</el-button>
              <el-button plain size="small" type="primary" icon="el-icon-search" @click="attachlog">{{plang.ATTACH_LOG_SEARCH}}</el-button>
              <el-button plain size="small" type="danger" icon="el-icon-delete" :disabled="this.sels.length===0" @click="deleteFiles">{{plang.COMMON_BUTTON_DELETE}}</el-button>
            </el-form-item>

            <el-form-item style="margin-bottom: 0px!important;">
              <el-input :placeholder="plang.FILE_A_SEARCH" v-model.trim="filters.search" size="small" tyle="width:auto;"><i slot="suffix" class="el-input__icon el-icon-search" v-on:click="getTables"></i>
              </el-input>
            </el-form-item>
            <a :href="blobUrl" download="" style="display:none;" ref="download"></a>
          </el-col>
          <el-col :span="12">
            <div style="padding:10px 0 10px 14px;">
              <b>{{plang.FILE_INDEX_A}}</b>
              <span style="font-size:12px;color:#bbb;margin-left:20px;"> {{plang.FILE_P_CAP}}</span>
              <el-progress :percentage="folder_capacity.capacity" style="width:200px;display: inline-block;"></el-progress>
              <span style="font-size:12px;color:#409eff;margin-left: -49px;"> {{folder_capacity.used}} / {{folder_capacity.total}}</span>
            </div>
          </el-col>
          <el-col :span="12" style="text-align:right;margin-top: 6px;line-height:28px;" v-if="total>0">
            <el-pagination :current-page="page" :page-sizes="[10, 20, 50]" :page-size="page_size" :total="total" v-if="total>0"
                           @size-change="f_TableSizeChange" @current-change="f_TableCurrentChange" layout="total, sizes, prev" style="display: inline-block">
            </el-pagination>
            <span>{{this.page+' / '+Math.ceil(this.total/this.page_size)}}</span>
            <el-pagination :current-page="page" :page-sizes="[10, 20, 50]" :page-size="page_size" :total="total" v-if="total>0"
                           @size-change="f_TableSizeChange" @current-change="f_TableCurrentChange" layout=" next,jumper" style="display: inline-block">
            </el-pagination>
          </el-col>
        </el-row>
      </el-form>

      <el-row>
        <el-table :data="listTables" tooltip-effect="dark" style="width: 100%;max-width:100%;height: 100%" @selection-change="f_TableSelsChange" :header-cell-style="{background:'#f0f1f3'}" size="mini">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column :label="plang.COMMON_NAME">
            <template slot-scope="scope">
              <el-row >
                <el-col :span="1" style="width:42px;padding-top:8px;">
                  <span class="bico" :class="scope.row.classObject"></span>
                </el-col>
                <el-col :span="20" style="font-size:16px;">
                  <div style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;" :title="scope.row.filename">{{scope.row.filename}}</div>
                  <div class="actions_a">
                    <span @click="zipRowDownload(scope.row)">{{plang.FILE_P_DOWNLOAD}}</span>
                    <span @click="$parent.sendMail_net(scope.row)">{{plang.FILE_P_SEND2}}</span>
                    <span @click="moveFormShow2(scope.row)">{{plang.FILE_A_TOP}}</span>
                    <span @click="renewalRowFile(scope.row)">{{plang.FILE_A_XUQI}}</span>
                    <span @click="$parent.preview(scope.row,'mail')" v-if="/.(gif|jpg|jpeg|png|bmp|svg|pdf|html|txt|xls|xlsx|doc|docx|ppt|pptx|xml|csv|md|log)$/.test(scope.row.filename.toLowerCase())">{{plang.COMMON_BUTTON_PREVIEW}}</span>
                    <span @click="deleteRowFiles(scope.row)" style="color:#f56c6c;">{{plang.COMMON_BUTTON_DELETE}}</span>
                  </div>
                </el-col>
              </el-row>
            </template>
          </el-table-column>

          <el-table-column :label="plang.COMMON_SIZE" width="100">
            <template slot-scope="scope">
              <span>{{scope.row.size| mailsize}}</span>
            </template>
          </el-table-column>



          <el-table-column :label="plang.FILE_P_TIME" width="200">
            <template slot-scope="scope">
              <i class="el-icon-time"></i>
              <span style="margin-left: 10px">{{scope.row.created.replace('T',' ')}}</span>
            </template>
          </el-table-column>

          <el-table-column :label="plang.ATTACH_LOG_TIMES" width="100">
            <template slot-scope="scope">
              <span >{{scope.row.times>0?scope.row.times:''}}</span>
            </template>
          </el-table-column>

          <el-table-column :label="plang.IS_ONLINE_ATTACH" width="100">
            <template slot-scope="scope">
              <i class="el-alert__icon el-icon-success" v-if="scope.row.is_online" style="color:#67C23A;"></i>
              <i class="el-alert__icon el-icon-error" v-else style="color:#F56C6C;"></i>
            </template>
          </el-table-column>

          <el-table-column :label="plang.FILE_A_TIME" width="220">
            <template slot-scope="scope">
              <span style="margin-left: 10px" v-if="$store.getters.getLanguage=='zh-hans'">{{scope.row.left_timestamp| validateLeft_zh}}</span>
              <span style="margin-left: 10px" v-if="$store.getters.getLanguage=='zh-tw'">{{scope.row.left_timestamp| validateLeft_zh_tw}}</span>
              <span style="margin-left: 10px" v-if="$store.getters.getLanguage=='en'">{{scope.row.left_timestamp| validateLeft_en}}</span>
            </template>
          </el-table-column>
          <el-table-column :label="plang.COMMON_MAIL_SUBJECT" >
            <template slot-scope="scope" >
              <div class="nowrap" :title="scope.row.subject">{{scope.row.subject}}</div>
            </template>
          </el-table-column>
        </el-table>
      </el-row>

      <el-dialog :title="plang.FILE_A_TOP"  :visible.sync="moveFormVisible"  :close-on-click-modal="false" :append-to-body="true">
        <el-form :model="moveForm" label-width="130px" :rules="moveFormRules" ref="moveForm">

          <el-form-item :label="plang.FILE_A_PATH" prop="folder_id" :error="folder_id_error">
            <el-select v-model="moveForm.folder_id" :placeholder="plang.FILE_A_PATH_PLACE" style="width: 100%">
              <el-option v-for="item in folder_fullpath" :key="item.id" :label="item.full_path" :value="item.id"></el-option>
            </el-select>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="moveFormVisible = false">{{plang.COMMON_BUTTON_CANCELL}}</el-button>
          <el-button type="primary" @click.native="moveFormSubmit()" :loading="moveFormLoading">{{plang.COMMON_BUTTON_SUBMIT}}</el-button>
        </div>
      </el-dialog>

      <el-dialog :title="plang.ATTACH_LOG"  :visible.sync="attachLogVisible"  :close-on-click-modal="false" :append-to-body="true">
        <el-table :data="attachlog_list">
          <el-table-column property="created" :label="plang.ATTACH_LOG_TIME" width="180"></el-table-column>
          <el-table-column property="ip" label="IP" width="120"></el-table-column>
          <el-table-column property="recipient" :label="plang.ATTACH_LOG_REC"></el-table-column>
          <el-table-column property="name" :label="plang.MAILBOX_COM_COMPOSE_FILENAME"></el-table-column>
          <el-table-column property="size" :label="plang.MAILBOX_COM_COMPOSE_FILESIZE" width="100">
            <template slot-scope="scope">
              <span>{{scope.row.size | mailsize}}</span>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          @size-change="change_size_attach"
          @current-change="change_page_attach"
          :current-page="page_attach"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pagesize_attach"
          layout="sizes, prev, pager, next"
          :hide-on-single-page="true"
          :total="total_attach">
        </el-pagination>

        <div slot="footer" class="dialog-footer">
          <el-button @click.native="attachLogVisible = false">{{plang.COMMON_CLOSE}}</el-button>

        </div>
      </el-dialog>

    </section>

  </div>
</template>

<script>
  import lan from '@/assets/js/lan';
  import {downloadAttach2, getAttach, moveAttach2Netdisk, downloadZipAttach, netdiskPathGet, mailAttachDelete, RenewalAttach,netdiskCapacityGet, attachlog } from '@/api/api'

  export default {
    data() {
      let _self = this;
      return {
        page_attach: 1,
        pagesize_attach: 10,
        total_attach: 0,
        attachLogVisible: false,
        attachlog_list: [],
        folder_capacity: {},
        sels: [],
        total: 0,
        page: 1,
        page_size: 10,
        listLoading: false,
        listTables: [],
        filters: {
          search: ''
        },

        blobUrl: '',

        folder_id_error: '',
        folder_fullpath: [],
        moveFormVisible: false,//编辑界面是否显示
        moveFormLoading: false,
        moveFormRules: {
          to_folder_id: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        moveForm:{ folder_id: -1, save_list: [] },

      }
    },
    computed:{
      plang(){
        let lang = lan.zh
        if(this.$store.getters.getLanguage=='zh-hans'){
          lang = lan.zh
        }else if(this.$store.getters.getLanguage=='zh-tw'){
          lang = lan.zh_tw
        }else if(this.$store.getters.getLanguage=='en'){
          lang = lan.en
        }else if(this.$store.getters.getLanguage=='es'){
          lang = lan.zh
        }else{
          lang = lan.zh
        }
        this.moveFormRules = {
          to_folder_id: [
            { required: true, message: lang.FILE_A_RULE, trigger: 'blur' },
          ]
        }
        return lang
      },
    },
    filters: {
      validateLeft_zh: function (timestamp) {
        let shijiancha = timestamp*1000;
        var days    = shijiancha / 1000 / 60 / 60 / 24;
        var daysRound   = Math.floor(days);
        var hours    = shijiancha/ 1000 / 60 / 60 - (24 * daysRound);
        var hoursRound   = Math.floor(hours);
        var minutes   = shijiancha / 1000 /60 - (24 * 60 * daysRound) - (60 * hoursRound);
        var minutesRound  = Math.floor(minutes);
        var seconds   = shijiancha/ 1000 - (24 * 60 * 60 * daysRound) - (60 * 60 * hoursRound) - (60 * minutesRound);
        let str = '';
        if(daysRound){
          str += daysRound+ '天 '
        }
        if(hoursRound){
          str += hoursRound+'时 '
        }
        if(minutesRound){
          str += minutesRound+'分 '
        }
        if(!daysRound && !hoursRound){
          return '即将过期'
        }
        if(daysRound>365 || (daysRound==365 && (hoursRound>0 ||minutesRound>0))){
          return '永久'
        }
        if(daysRound>1){
          return daysRound+ '天'
        }
        return str;
        return timestamp;
        var day = timestamp/86400;
        return day;

        var date = new Date(inputTime);
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        var second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + '　' + h + ':' + minute + ':' + second;
      },
      validateLeft_zh_tw: function (timestamp) {
        let shijiancha = timestamp*1000;
        var days    = shijiancha / 1000 / 60 / 60 / 24;
        var daysRound   = Math.floor(days);
        var hours    = shijiancha/ 1000 / 60 / 60 - (24 * daysRound);
        var hoursRound   = Math.floor(hours);
        var minutes   = shijiancha / 1000 /60 - (24 * 60 * daysRound) - (60 * hoursRound);
        var minutesRound  = Math.floor(minutes);
        var seconds   = shijiancha/ 1000 - (24 * 60 * 60 * daysRound) - (60 * 60 * hoursRound) - (60 * minutesRound);
        let str = '';
        if(daysRound){
          str += daysRound+ '天 '
        }
        if(hoursRound){
          str += hoursRound+'时 '
        }
        if(minutesRound){
          str += minutesRound+'分 '
        }
        if(!daysRound && !hoursRound){
          return '即将过期'
        }
        if(daysRound>365 || (daysRound==365 && (hoursRound>0 ||minutesRound>0))){
          return '永久'
        }
        if(daysRound>1){
          return daysRound+ '天'
        }
        return str;
        return timestamp;
        var day = timestamp/86400;
        return day;

        var date = new Date(inputTime);
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        var second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + '　' + h + ':' + minute + ':' + second;
      },
      validateLeft_en: function (timestamp) {
        let shijiancha = timestamp*1000;
        var days    = shijiancha / 1000 / 60 / 60 / 24;
        var daysRound   = Math.floor(days);
        var hours    = shijiancha/ 1000 / 60 / 60 - (24 * daysRound);
        var hoursRound   = Math.floor(hours);
        var minutes   = shijiancha / 1000 /60 - (24 * 60 * daysRound) - (60 * hoursRound);
        var minutesRound  = Math.floor(minutes);
        var seconds   = shijiancha/ 1000 - (24 * 60 * 60 * daysRound) - (60 * 60 * hoursRound) - (60 * minutesRound);
        let str = '';
        if(daysRound){
          str += daysRound+ ' day(s) '
        }
        if(hoursRound){
          str += hoursRound+' hour(s) '
        }
        if(minutesRound){
          str += minutesRound+' minute(s) '
        }
        if(!daysRound && !hoursRound){
          return 'will expired'
        }
        if(daysRound>365 || (daysRound==365 && (hoursRound>0 ||minutesRound>0))){
          return 'forever'
        }
        if(daysRound>1){
          return daysRound+ ' day(s)'
        }
        return str;
        return timestamp;
        var day = timestamp/86400;
        return day;

        var date = new Date(inputTime);
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        var second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + '　' + h + ':' + minute + ':' + second;
      },
    },

    created: function () {
      this.getTables();
    },

    methods: {
      change_page_attach(val){
        this.page_attach = val;
        this.getAttachLog();
      },
      change_size_attach(val){
        this.pagesize_attach = val;
        this.getAttachLog();
      },
      attachlog(){
        this.page_attach = 1;
        this.getAttachLog();
      },
      getAttachLog(){
        let param ={
          page:this.page_attach,
          page_size:this.pagesize_attach
        }
        attachlog(param).then(res=>{
          console.log(res)
          this.total_attach = res.data.count;
          this.attachlog_list = res.data.results
          this.attachLogVisible = true;
        }).catch(err=>{
          console.log(err)
        })
      },
      getCapacity: function(){
        netdiskCapacityGet().then(res=>{
          if(res.data.capacity =='0%'){
            res.data.capacity = 0
          }
          if(res.data.capacity > 100){
            res.data.capacity = 100
          }
          if(res.data.capacity < 0){
            res.data.capacity = 0
          }
          this.folder_capacity = res.data;
        });
      },
      f_TableSelsChange: function (sels) {
        this.sels = sels;
      },
      // 每页数目改变
      f_TableSizeChange(val) {
        this.page_size = val;
        this.getTables();
      },
      // 翻页改变
      f_TableCurrentChange(val) {
        this.page = val;
        this.getTables();
      },
      getTables: function(){
        this.listLoading = true;
        let param={
          limit:this.page_size,
          offset:(this.page-1)*this.page_size,
          search: this.filters.search,
        };
        getAttach(param).then(res=>{
          this.total = res.data.count;
          this.listTables = res.data.results;
          for(let i=0;i<this.listTables.length;i++){
            let o = this.listTables[i];
            let index = o.filename.lastIndexOf('.');
            let str = 'bf'+o.filename.slice(index+1).toUpperCase();
            o['classObject']={};
            o['classObject'][str] = true;
          }
          this.listLoading = false;
        }).catch(()=>{
          this.listLoading = false;
        });

        this.getCapacity();
      },
      deleteFiles(){
        let that = this;
        var ids = this.sels.map(item => item.id);
        this.$confirm(this.plang.FILE_A_MSG1, this.plang.COMMON_BUTTON_CONFIRM_NOTICE, {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          let para = {ids: ids};
          mailAttachDelete(para).then((response)=> {
            this.listLoading = false;
            that.$message({ message: this.plang.COMMON_DELETE_SUCCESS, type: 'success' });
            if((this.page-1)*this.page_size >= (this.total-ids.length)){
              this.page = 1;
            }
            this.getTables();
          }).catch( (err) =>{
            this.listLoading = false;
            console.log(err)
          });
        }).catch(()=>{

        });
      },
      deleteRowFiles(row){
        let that = this;
        this.$confirm(this.plang.FILE_A_MSG2, this.plang.COMMON_BUTTON_CONFIRM_NOTICE, {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          let para = {ids: [row.id]};
          mailAttachDelete(para).then((response)=> {
            this.listLoading = false;
            that.$message({ message: this.plang.COMMON_DELETE_SUCCESS, type: 'success' });
            if((this.page-1)*this.page_size >= (this.total-1)){
              this.page = 1;
            }
            this.getTables();
          }).catch( (err)=> {
            this.listLoading = false;
            console.log(err)
          });
        }).catch(()=>{

        });
      },

      renewalRowFile(row){
        let that = this;
        this.listLoading = true;
        RenewalAttach(row.id).then((response)=> {
          this.listLoading = false;
          that.$message({ message: this.plang.FILE_A_MSG3, type: 'success' });
          this.getTables();
        }).catch( (err)=> {
          this.listLoading = false;
          console.log(err)
          this.getTables();
        });
      },

      //点击下载
      download(){
        this.$refs.download.click();
      },
      zipRowDownload: function (row) {
        let that = this;
        this.listLoading = true;
        downloadAttach2(row.id, {download: true}).then((response)=> {
          this.listLoading = false;
          let blob = new Blob([response.data], { type: response.headers["content-type"] })
          let objUrl = URL.createObjectURL(blob);
          this.blobUrl = objUrl;
          // let filenameHeader = response.headers['content-disposition']
          // let filename = filenameHeader.slice(filenameHeader.indexOf('=')+2,filenameHeader.length-1);
          let filename = row.filename;
          if (window.navigator.msSaveOrOpenBlob) {
            // if browser is IE
            navigator.msSaveBlob(blob, filename);//filename文件名包括扩展名，下载路径为浏览器默认路径
          } else {
            // var encodedUri = encodeURI(csvContent);//encodeURI识别转义符
            var link = document.createElement("a");
            link.setAttribute("href", objUrl);
            link.setAttribute("download", filename);
            document.body.appendChild(link);
            link.click();
          }
          that.$message({ message: this.plang.COMMON_EXPORT_SUCCESS, type: 'success' });
          this.getTables();
          // this.getPabs();
        }).catch( (err)=> {
          this.listLoading = false;
          console.log(err)
        });
      },
      zipDownload: function(){
        let that = this;
        this.listLoading = true;
        var ids = this.sels.map(item => item.id);
        downloadZipAttach({ids: ids}).then((response)=> {
          this.listLoading = false;
          let blob = new Blob([response.data], { type: response.headers["content-type"] })
          let objUrl = URL.createObjectURL(blob);
          this.blobUrl = objUrl;
          // let filenameHeader = response.headers['content-disposition']
          // let filename = filenameHeader.slice(filenameHeader.indexOf('=')+2,filenameHeader.length-1);
          let filename = 'attachment.zip';
          if (window.navigator.msSaveOrOpenBlob) {
            // if browser is IE
            navigator.msSaveBlob(blob, filename);//filename文件名包括扩展名，下载路径为浏览器默认路径
          } else {
            // var encodedUri = encodeURI(csvContent);//encodeURI识别转义符
            var link = document.createElement("a");
            link.setAttribute("href", objUrl);
            link.setAttribute("download", filename);
            document.body.appendChild(link);
            link.click();
          }
          that.$message({ message: this.plang.COMMON_EXPORT_SUCCESS, type: 'success' });
          this.getTables();
          // this.getPabs();
        }).catch( (err) =>{
          this.listLoading = false;
          console.log(err)
        });
      },

      moveFormShow: function () {
        this.folder_id_error = '';
        netdiskPathGet().then(res=> {
          this.folder_fullpath = res.data;
          let form = this.moveForm;
          form.save_list = this.sels.map(item => item.id);
          this.moveForm = Object.assign({}, form);
          this.moveFormVisible = true;
          this.moveFormLoading = false;
        });
      },
      moveFormShow2: function (row) {
        this.folder_id_error = '';
        netdiskPathGet().then(res=> {
          this.folder_fullpath = res.data;
          let form = this.moveForm;
          form.save_list = [row.id];
          this.moveForm = Object.assign({}, form);
          this.moveFormVisible = true;
          this.moveFormLoading = false;
        });
      },
      moveFormSubmit: function(){
        this.folder_id_error='';
        this.$refs.moveForm.validate((valid) => {
          if (valid) {
            this.moveFormLoading = true;
            let para = Object.assign({}, this.moveForm);
            moveAttach2Netdisk(para)
              .then((res) => {
                this.$refs['moveForm'].resetFields();
                this.moveFormLoading = false;
                this.moveFormVisible = false;
                this.$message({message: this.plang.COMMON_SUBMIT_SUCCESS, type: 'success'});
                this.getTables();
              })
              .catch( (err)=> {
                this.moveFormLoading = false;
                if("non_field_errors" in err) {
                  this.folder_id_error = err.non_field_errors[0];
                }
                console.log(err)

              });
          }
        });
      },

    },

  }
</script>
<style>
  .clickable{
    color:#409EFF;
    cursor:pointer;
  }
  .folder_type{
    cursor:pointer;
  }
  .folder_type:hover{
    cursor:pointer;
    color:#409EFF;
    font-weight: bold;
  }
  .bico{
    display:inline-block;
    width: 32px;
    height: 32px;
    background-image: url(../../assets/img/icons.png);
    background-position: -672px -352px;
  }
  .actions_a>span{
    color: #0479bc;
    margin-right:10px;
    cursor:pointer;
    font-size:12px;
  }
  .el-progress__text{
    display: none!important;
  }
  .nowrap{
    overflow: hidden;
    word-wrap: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
</style>




